  transform:rotate(6deg) ;
  }
</style>
<script>
  var interval;
  import vHead from '../components/Header.vue';
  import vFoter from '../components/footer.vue';
  export default {
    components: {
      vHead,
      vFoter
    },
    beforeCreate(){
      if(!this.$store.state.loading){
        this.$store.state.loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: '#fff',
          clearInterval:''
        });
      }
    },
    data () {
      return {
        imgCount49:49,
        dialogVisible:false,
        imgsCount:0,
        loadImgCount:0
      }
    },
    mounted(){
         //监听所有图片加载完成
        var that=this;
        let imgs=document.querySelectorAll("img");
        that.imgsCount=imgs.length;
        console.log(imgs.length)
        imgs.forEach((item)=>{
          let img = new Image();
          img.src=item.getAttribute('src');
          img.onload = ()=>{
            that.loadImgCount++
          }
          img.onerror=()=>{
            that.loadImgCount++
          }
        })
     },
    watch:{
      loadImgCount (val,oldval) {
        if (val == this.imgsCount) {
          console.log("加载完毕");
          this.timeOutImg();
          this.$store.state.loading.close();
          $(".app-box.index").show();
        }
      }

    },
    destroyed() {//销毁后
      clearInterval(this.clearInterval);
      clearInterval(interval);
    },
    methods: {
      timeOutImg(){
        let index=0;
        this.clearInterval=setInterval(() => {
          //console.log(index)
           $(".home-top .right .a1 img").hide();
           $(".home-top .right .a1 img").eq(index).show();
          if(index==48){
            index=0;
          }else {
            index++;
          }
        },100)
      },
      videoShow(){
        this.dialogVisible=true;
        setTimeout(() => {
          $("iframe").attr("src","./static/video/index.html");
        })
      },
      handleClose(done) {
        done();
        $("iframe").attr("src","");
      },
      resizeModel(className,height,width){
        let innerRatio=$(window).width()/width;
        $("."+className).css({
          "transform": "scale(" + innerRatio + ")",
          "-ms-transform": "scale(" + innerRatio + ")",
          "-moz-transform": "scale(" + innerRatio + ")",
          "-webkit-transform": "scale(" + innerRatio + ")",
          "-o-transform": "scale(" + innerRatio + ")",
          "height":height*innerRatio+60
        });
      },
      setSwiper(){
        var mySwiper = new Swiper('#swiper1',{
          pagination : '.swiper-pagination',
          loop : true,
          paginationClickable :true,
          autoplay : 2000,
        })
      },
      setSwiper2(){
        var mySwiper2 = new Swiper('.swiper2',{
          loop : true,
        })
        $('.swiper2 .swiper-button-prev').click(function(){
          mySwiper2.swipePrev();
        })
        $('.swiper2 .swiper-button-next').click(function(){
          mySwiper2.swipeNext();
        })
      },
      startAutoPlay(){
        return setInterval(function () {
          jQuery('#featured ul').roundabout_animateToNextChild();
        }, 3000);
      },
      setRoundabout(){
        let that=this;

        $('#featured ul')
          .roundabout({
              easing: 'easeOutInCirc',
              duration: 200, // 运动速度
              btnPrev: ".ban_r_btn", // 右按钮
              btnNext: ".ban_l_btn", // 左按钮
              minScale:0.6,
              minOpacity: 1,         //最小的透明度
               maxOpacity: 1,         //最大的透明度
              enableDrag: true       // 在移动端可以拖拽播放
            }
          )
          .hover(
            function () {
              clearInterval(interval);
            },
            function () {
              interval = that.startAutoPlay();
            }
          );
        interval = that.startAutoPlay();
      }

    },
    created: function () {
      this.$store.state.navState=0;
      let that=this;

      this.$nextTick( () => {

        if($(window).width()<650){
          that.resizeModel("module2",800,1266); //模块2
          that.resizeModel("module3",500,1336); //模块3
          that.resizeModel("module4",700,1373); //模块4
          that.resizeModel("module5",750,1373); //模块5
        }else {
          that.resizeModel("module2",1000,1920); //模块2
          that.resizeModel("module3",700,1920); //模块3
          that.resizeModel("module4",900,1920); //模块4
          that.resizeModel("module5",950,1920); //模块5
        }



        $(window).resize(function () {
          that.resizeModel("module2",1000,1920); //模块2
          that.resizeModel("module3",700,1920); //模块3
          that.resizeModel("module4",900,1920); //模块4
          that.resizeModel("module5",950,1920); //模块5
        })

        that.setSwiper();
        that.setSwiper2();
        that.setRoundabout();
      })


    }
  }
</script>





